.trainers {
  width: 100vw;
  min-width: $vp-1200;
  padding: 0 0 12px 0;

  @media (max-width: $vp-1199) {
    min-width: 768px;
    padding: 0 0 27px 0;
  }

  @media (max-width: $vp-767) {
    min-width: 320px;
    padding: 0;
    margin: 0 0 -55px 0;
  }
}

.trainers > div {
  width: 100%;
  max-width: 1500px;
  height: 500px;
  margin: 0 auto;
  padding: 15px 7.54% 80px 7.54%;
  position: relative;

  @media (min-width: 1367px) {
    padding: 15px 103px 90px 103px;
  }

  @media (max-width: $vp-1199) {
    max-width: 880px;
    padding: 50px 100px 100px 100px;
  }

  @media (max-width: $vp-767) {
    max-width: 360px;
    padding: 22px 47px 68px 47px;
  }
}

.trainers h2 {
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-weight: 600;
  font-size: 54px;
  line-height: 65px;
  letter-spacing: 0.03em;
  margin: 0 0 47px 2px;

  @media (max-width: $vp-1199) {
    font-size: 42px;
    line-height: 50px;
    text-align: center;
    margin: 0 2px 32px 0;
  }

  @media (max-width: $vp-767) {
    font-size: 32px;
    line-height: 38px;
    margin: 0 2px 45px 0;
  }
}

.trainers__list {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.trainers__item {
  width: 25.41%;
  height: 294px;
  position: relative;
  background-color: #263b65;
  background-repeat: no-repeat;
  background-size: 100% 100%;

  @media (max-width: $vp-1199) {
    width: 47.18%;
  }

  @media (max-width: $vp-767) {
    width: 100%;
    max-width: 450px;
    height: 274px;
  }
}

.trainers__item--1 {
  background-image: url("../img/trainer-1.jpg");

  @media (min-resolution: 2dppx) {
    background-image: url("../img/trainer-1@2x.jpg");
    background-size: 100% 100%;
  }
}

.trainers__item--2 {
  background-image: url("../img/trainer-2.jpg");

  @media (min-resolution: 2dppx) {
    background-image: url("../img/trainer-2@2x.jpg");
    background-size: 100% 100%;
  }
}

.trainers__item--3 {
  background-image: url("../img/trainer-3.jpg");

  @media (min-resolution: 2dppx) {
    background-image: url("../img/trainer-3@2x.jpg");
    background-size: 100% 100%;
  }
}

.trainers__item--4 {
  background-image: url("../img/trainer-4.jpg");

  @media (min-resolution: 2dppx) {
    background-image: url("../img/trainer-4@2x.jpg");
    background-size: 100% 100%;
  }
}

.trainers__item--5,
.trainers__item--6,
.trainers__item--7,
.trainers__item--8 {
  background-image: url("../img/trainer-4.jpg");

  @media (min-resolution: 2dppx) {
    background-image: url("../img/trainer-4@2x.jpg");
    background-size: 100% 100%;
  }
}

.trainers__name {
  font-weight: 700;
  font-size: 18px;
  line-height: 27px;
  margin: 0;
  position: absolute;
  top: 82.8%;
  left: 50%;
  transform: translate(-50%, 0);
  color: #ffffff;
}

.trainers__extra {
  display: none;
  width: 100%;
  height: 294px;
  padding: 54px 45px 50px 45px;
  text-align: center;
  overflow-y: scroll;

  @media (max-width: $vp-767) {
    height: 274px;
    padding: 44px 25px 30px 25px;
  }
}

.trainers__item:hover .trainers__extra {
  display: block;
  background-color: rgba(16, 38, 84, 0.89);
  color: #ffffff;
}

.trainers__item:hover .trainers__name {
  display: none;
}

.trainers__extra-name {
  margin: 0;
  font-weight: 700;
  font-size: 18px;
  line-height: 21px;

  @media (max-width: $vp-1199) {
    margin: 0 4px 0 0;
  }

  @media (max-width: $vp-767) {
    margin: 0;
  }
}

.trainers__extra-speciality {
  margin: 0 0 32px 0;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: #b6bdcc;
}

.trainers__extra-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.trainers__extra-item {
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  text-align: left;
  padding: 0 0 0 25px;
  position: relative;

  &::after {
    content: "";
    display: block;
    width: 6px;
    height: 6px;
    background-color: #ffffff;
    border-radius: 50%;
    position: absolute;
    top: 8px;
    left: 8px;
  }
}

.trainers__extra-item:not(:last-child) {
  margin: 0 0 13px 0;
}

.trainers button {
  width: 23px;
  height: 23px;
  border: none;
  border-bottom: 2px solid #afafaf;
  border-left: 2px solid #afafaf;
  background-color: transparent;
  cursor: pointer;
  position: absolute;
  z-index: 100;

  @media (max-width: $vp-767) {
    width: 12px;
    height: 14px;
  }

  &:hover {
    border-bottom: 2px solid #1c3374;
    border-left: 2px solid #1c3374;
  }

  &:active {
    border-bottom: 2px solid #1c3374;
    border-left: 2px solid #1c3374;
    opacity: 0.5;
  }

  &:disabled {
    cursor: default;
    border-bottom: 2px solid #e6e6e6;
    border-left: 2px solid #e6e6e6;
  }
}

.trainers__button--previous {
  transform: rotate(45deg);
  top: 52%;
  left: 3.7%;

  @media (max-width: $vp-1199) {
    top: 53%;
    left: 6.2%;
  }

  @media (max-width: $vp-767) {
    top: 47%;
    left: 5%;
  }
}

.trainers__button--next {
  transform: rotate(225deg);
  top: 52%;
  left: 94.8%;
  user-select: none;

  @media (max-width: $vp-1199) {
    top: 53%;
    left: 90.9%;
  }

  @media (max-width: $vp-767) {
    top: 47%;
    left: 90%;
  }
}
